
#app {
  width: 100%;
  height: 100%;
  padding-left: 300px;
  padding-top: 60px;
}

#nav {
    width: 300px;
	height: 100%;
    position: fixed;
	top: 0;
	left: 0;
	z-index: 99;
	background-color: #66cc66;
    padding: .8rem;
    padding-top: 60px;
    overflow-y: auto;
    overflow-x: hidden;
}
#nav a {
    text-decoration: none;
    color: #FFFFCC;
    font-size: .8rem;
    line-height: 1.1rem;
}
#nav h2 {
    font-size: .85rem;
    color: #FFFFCC;
    font-weight: normal;
    padding: .4rem 0;
}
.subnav {
    padding-left: .8rem;
}
#nav .subnav .router-link-active {
    color: #CC6633;
}

header {
    width: 100%;
	height: 60px;
	line-height: 60px;
	font-size: 1.3rem;
	color: #fff;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 999;
	background-color: #66CC99;
	-webkit-box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.3);
	        box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.3);
	padding-left: 2rem;
}
#logo {
    height: 100%;
}

#main {
  padding-right: 160px;
  width: 100%;
  position: relative;
  top: 0;
  left: 0;
}
#content {
  padding: .8rem 1.3rem;
  padding-left: 1.5rem;
}
#content p {
  font-size: .8rem;
  line-height: 1.2rem;
  padding: .4rem;
  margin-bottom: .25rem;
}
#content li {
  font-size: .8rem;
  line-height: 1.2rem;
}
.content a {
  text-decoration: none;
  color: #6600CC;
}
pre {
  background-color: #CCE8CF;
  padding: .5rem;
  line-height: 1.1rem;
  border-radius: 5px;
}
#table-of-contents {
  width: 160px;
  position: fixed;
  top: 0;
  right: 0;
  height: 100%;
  font-size: .6rem;
  padding-top: 60px;
  overflow-y: auto;
  border-left: 1px solid #99CCCC;
}
#table-of-contents h2 {
  font-size: .7rem;
  padding: .6rem 0 .4rem 0;
}
#content #table-of-contents li {
  font-size: .6rem;
  line-height: .8rem;
}
#text-table-of-contents {
  padding-left: .3rem;
}
#text-table-of-contents a {
  text-decoration: none;
  color: #9966CC;
  font-size: .6rem;
  line-height: .8rem;
}
#text-table-of-contents > ul > li > ul{
  padding-left: .6rem;
}
